import { useRecoilValue } from "recoil"
import { iconState } from "../atoms/iconState"

export const Profile = () => {

  const icon = useRecoilValue(iconState)

  return(
    <div className="plofile">
        <h2>Profile</h2>
        <img src={icon} alt="" className="icon"/>
        <ul>
          <li>
            <h3></h3>
            <p></p>
            <h3></h3>
            <p>1993 62</p>
          </li>
        </ul>
    </div>
  )
}